h3 {
  font-size: 22px;
  color: tomato;
}

@media only screen and (max-width: 767px) {
  h3 {
    font-size: 18px;
  }
}

.pick {
  margin-top: 0;
  padding-top: 22vh;
}

.pick article {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 2vh;
}

.pick article ul {
  display: -webkit-flex;
  display: flex;
  padding: 0;
  margin: 0;
  width: 80vw;
}

.pick article li {
  text-align: center;
  width: 33%;
  list-style-type: none;
}

.pick article li img {
  width: 100%;
  box-sizing: border-box;
  padding: 30px;
}

@media only screen and (max-width: 767px) {
  .pick {
    padding-top: 30vh;
  }

  .pick article li img {
    padding: 10px;
  }
}

@media only screen and (max-width: 500px) {
  .pick {
    padding-top: 20vh;
  }

  .pick article ul {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .pick article li {
    width: 100%;
  }
}

.pick article ul li img {
  -webkit-transition: 0.2s opacity ease-in-out;
  transition: 0.2s opacity ease-in-out;
}

.pick article ul:hover li img {
  opacity: 0.4;
}

.pick article ul:hover li:hover img {
  opacity: 1;
}

.check {
  margin-top: 0;
  padding-top: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 1.5vw;
  -webkit-align-items: center;
          align-items: center;
}

.check .left {
  width: 30vw;
}

.check .right {
  width: 40vw;
  padding-left: 5vw;
}

.check img {
  width: 100%;
}

@media only screen and (max-width: 992px) {
  .check {
    font-size: 2vw;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding-top: 18vh;
  }

  .check .left {
    width: 100vw;
  }

  .check .left img {
    width: 30%;
  }

  .check .right {
    padding-left: 0;
    width: 70vw;
  }
}

@media only screen and (max-width: 767px) {
  .check {
    font-size: 18px;
  }
}

@media only screen and (max-width: 500px) {
  .check .left {
    width: 60vw;
  }

  .check .left img {
    width: 100%;
  }

  .check .right {
    width: 90vw;
  }
}

.msg {
  border: none;
  color: #000;
  width: 94%;
  letter-spacing: 1px;
  font-size: 2vw;
  font-family: 'Comic Sans MS', 'Comic Sans', monospace;
  text-align: center;
  display: block;
  margin: 25px 0;
  padding: 15px;
  background-color: mistyrose;
}

@media only screen and (min-width: 992px) {
  .msg {
    font-size: large;
  }
}

@media only screen and (max-width: 767px) {
  .msg {
    font-size: small;
  }
}

#punchline {
  font-size: 25px;
}

section {
  padding: 0 25px;
  margin-top: 20vh;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  section {
    padding-bottom: 25vw;
  }

  section.start,
  section.check {
    padding-bottom: 0;
  }
}

section.start {
  padding: 0;
  margin-top: 0;
  height: 95vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

section.start img {
  width: 70%;
}

@media only screen and (max-width: 992px) {
  section.start img {
    width: 80%;
  }
}

@media only screen and (max-width: 767px) {
  section.start {
    height: 85vh;
  }
  section.start img {
    width: 100%;
  }
}

#sundes {
  font-size: 15px;
  position: absolute;
  top: 10px;
  right: 10px;
}

#sundes a {
  text-decoration: none;
  color: powderblue;
}

#sundes a:hover {
  font-style: normal;
}

section.start + #sundes {
  display: none;
}

@font-face {
	font-family: 'Comic Sans';
	src: url(/ily/static/media/Comic-Sans-MS.19a8e4fe.ttf) format('truetype');
}

body {
  margin: 0;
  padding: 0;
  letter-spacing: 1px;
  font-family: 'Comic Sans MS', 'Comic Sans', monospace;
}

h1, h2 {
  margin: 0;
}

h1 {
  letter-spacing: 4px;
  text-transform: lowercase;
  position: fixed;
  font-size: 45px;
  top: 4vw;
  right: 6vw;
}

a {
  color: #000;
  text-decoration: underline;
}

a:hover {
  padding-bottom: 0;
  border-bottom: none;
  font-style: italic;
}

h1 a {
  color: gold;
  text-decoration: none;
}

h1 a:hover {
  font-style: italic;
}

h2 {
  font-weight: normal;
  font-size: 25px;
}

p:nth-child(1),
p:nth-child(1) a,
p:nth-child(1) input {
  color: mediumspringgreen;
}

p:nth-child(2),
p:nth-child(2) a,
p:nth-child(2) input {
  color: purple;
}


p:nth-child(3),
p:nth-child(3) a,
p:nth-child(3) input {
  color: orange;
}

